*{ padding: 0px; margin: 0px; box-sizing: border-box; text-decoration: none; font-family: 'Poppins', sans-serif;}
html{ scroll-behavior: smooth;}
a{ text-decoration: none;}
li{ list-style-type: none;}

/* Navbar Change default bootstrap */
.navbar-toggler:focus { box-shadow: none !important;  }
.navbar-light .navbar-toggler { border: none !important;}
header .navbar .navbar-collapse ul li a { font-size: 16px !important;}
.nav-link { padding: 0 1rem !important; }
.navbar { padding-top: 1rem !important; padding-bottom: 1rem !important; }
a.nav-link { text-align: center; }

/* Navbar icon  */
.change-icon > .far + .far, .change-icon:hover > .far {
    display: none;
}

.change-icon:hover > .far + .far {
    display: inherit;
  }

/* Additional Css */
.section-padding { padding: 60px 0;}
.text-body{font-size: 18px;}
.text-crimson{color: crimson;}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 { line-height: 1.5 !important;}



/* Navigation Bar  *//* Navigation Bar  */

