header .logo{ font-size: 25px; font-weight: 700; letter-spacing: 2px; }
header .logo::first-letter{ font-size: 200%; color: crimson; }
header .navbar { background : rgba(219, 216, 216, 0.562); }

/* Banner Section Start *//* Banner Section Start */
.home .home-content .text-1{ font-size: 27px;}
.home .home-content .text-2{ font-size: 75px; font-weight: 600; margin-left: -3px;}
.home .home-content .text-3{ font-size: 40px; margin: 5px 0;}
.home .home-content .text-3 span{ color: crimson; font-weight: 500;}
.home .home-content a{ display: inline-block; background: crimson; color: #fff; font-size: 25px; padding: 12px 36px; margin-top: 20px; font-weight: 400; border-radius: 6px;  border: 2px solid crimson; transition: all 0.3s ease; }
.home .home-content a:hover{ color: crimson; background: none;}
.home .hire-btn:hover{text-decoration: none;}

/* About me Section Start *//* About me Section Start */
.about-me{ background-color: rgba(219, 216, 216, 0.562); }
.section-header{font-size: 36px; }
.section-description{ font-size: 28px; color: crimson; }
.personal-photo{ border-top-left-radius: 20%;  border-bottom-right-radius: 20%;  border-top-right-radius: 5px;  border-bottom-left-radius: 25px; }
