.typing-2{ color: crimson;}

/* cv section  *//* cv section */
.download-title{ color: black; font-size: 40px; font-weight: 600;  }
.download-btn{ display: inline-block; border: 4px solid black; padding: 15px 30px; font-size: 22px; color: black; margin-top: 20px; }
.download-btn:hover{ border: 4px solid crimson; color: white; background-color: crimson; transition: 1s; text-decoration: none;}

/* Contact Form *//* Contact Form */
.contact-me { background: rgba(219, 216, 216, 0.562);}
.contact-me .input-name , .contact-me .input-email , .contact-me .input-subject , .contact-me .input-text-area , .contact-me .input-name , .contact-me .input-send-btn{  width: 100%; }
.contact-me .input-name , .contact-me .input-email , .contact-me .input-subject{ height: 45px; width: 100%; border: 1px solid lightgrey; border-radius: 6px; outline: none; padding: 0 15px; font-size: 17px; transition: all 0.3s ease;}
.contact-me .input-text-area{ height: 100px; width: 100%; border: 1px solid lightgrey; border-radius: 6px; outline: none; padding: 0 15px; font-size: 17px; transition: all 0.3s ease;}
.contact-me .input-send-btn{ color: #fff;  display: block; width: 160px!important; height: 45px; outline: none; font-size: 18px; font-weight: 500; border-radius: 6px; cursor: pointer; flex-wrap: nowrap; background: crimson; border: 2px solid crimson; transition: all 0.3s ease;}


/* Copyright section *//* Copyright section */
footer { background-color: #111111; }



/*New  Skills Section*/
.single-skill:hover { border: none; background: #d3d3d338; transform: translateY(5px);}
.single-skill {  margin: 20px 5px; padding: 20px; box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px; transition: .7s;}
.react-logo > img {  animation: rotation 10s infinite linear;}

@keyframes rotation {
    from { transform: rotate(0deg); }
    to { transform: rotate(359deg); }
}

/* New Time Line Section */
.timeline-bg{  background-color: #474e5d;}
.timeline { position: relative; max-width: 1200px; margin: 0 auto;}
/* The actual timeline (the vertical ruler) */
.timeline::after { content: ''; position: absolute; width: 6px; background-color: white; top: 0; bottom: 0; left: 50%; margin-left: -3px;}
/* timeline-block around content */
.timeline-block {  padding: 10px 40px;  position: relative;  background-color: inherit;  width: 50%;}
  /* The circles on the timeline */
.timeline-block::after { content: '';  position: absolute;  width: 25px;  height: 25px;  right: -13px;  background-color: white;  border: 4px solid #FF9F55;  top: 15px; border-radius: 50%;  z-index: 1;}
 /* Place the timeline-block to the left */
.left {  left: 0;}
/* Place the timeline-block to the right */
.right { left: 50%; }
/* Add arrows to the left timeline-block (pointing right) */
.left::before { content: " ";  height: 0;  position: absolute;  top: 22px;  width: 0;  z-index: 1;  right: 30px;  border: medium solid white;  border-width: 10px 0 10px 10px;  border-color: transparent transparent transparent white;}
/* Add arrows to the right timeline-block (pointing left) */
.right::before {  content: " ";  height: 0;  position: absolute;  top: 22px;  width: 0;  z-index: 1;  left: 30px;  border: medium solid white;  border-width: 10px 10px 10px 0;  border-color: transparent white transparent transparent;}
/* Fix the circle for timeline-blocks on the right side */
.right::after {  left: -13px;}
/* The actual content */
.content {  padding: 20px 30px;  background-color: white;  position: relative;  border-radius: 6px;}
  
 /* About page info */
.circle-photo > img {   border-radius: 25%;  height: 300px; width: 400px;}
.sec-links {  border-top: 1px solid lightgrey; }
/* Projects Section */
.project-border { border-top: 1px solid lightgrey; border-bottom: 1px solid lightgrey;}


