.projects .project-img{ min-height: 250px; }
.navigation .nav-item{padding:0px !important;}
.text-body{font-size: 16px;}
.home .home-content .text-1{ font-size: 22px;}
.home .home-content .text-2{ font-size: 45px; }
.home .home-content .text-3{ font-size: 26px; }
