.heading-text{font-size: 22px;}
.input-name{margin-bottom: 15px;}
.contact-me .message-header{ margin-top: 20px;}
.navbar { padding-top: .5rem !important; padding-bottom: .5rem !important; }
}

 /* Media queries - Responsive timeline on screens less than 600px wide */
 @media screen and (max-width: 600px) {
/* Place the timelime to the left */ 
.timeline::after { left: 31px; }
/* Full-width timeline-blocks */
.timeline-block { width: 100%; padding-left: 70px; padding-right: 25px; }
/* Make sure that all arrows are pointing leftwards */
.timeline-block::before { left: 60px; border: medium solid white; border-width: 10px 10px 10px 0; border-color: transparent white transparent transparent; }
/* Make sure all circles are at the same spot */
.left::after, .right::after { left: 18px; }
/* Make all right timeline-blocks behave like the left ones */
.right { left: 0%; }
}

@media only screen and (max-width:575px){
    .download-btn { padding: 8px 16px; }
    .download-title {  font-size: 30px; margin-top: 20px; }
    .contact-me .sub-title { font-size: 14px;}
    footer {font-size: 14px;}
    .personal-photo { height: 250px; width: 250px; border-radius: 50%;}
}

