.inner-project-img {  position: relative;}
div.layer:hover { background: rgba(0,0,0,.71); opacity: 1;}
div.layer {  width: 100%; font-weight: 500; color: #000; bottom: 0; left: 50%; transform: translateX(-50%); position: absolute; opacity: 0; transition: .5s; padding: 5px; height: 100%; max-width: 100%; top: 0; }
.project-title { font-size: 25px; color: #fff; position: absolute; width: 100%; left: 50%; top: 50%; transform: translate(-50%,-50%); text-align: center; font-weight: 700;}

/* Skeleton Loading */
.skeleton { 
    animation: skeleton-loading 1s linear infinite alternate;
}

@keyframes skeleton-loading { 
    0%{
        background-color: hsl(200, 20%,70%);
    }

    100% {
        background-color: hsl(200,20%,95%);
    }
}